import React from 'react';
import './Navbar.css';
import { Link } from 'react-router-dom';

function Navbar() {
  return (
    <div className='container-fluid px-lg-5'>
      <div className='navbar navbar-expand'>
        <div className='container-fluid'>
          <ul className='navbar-nav'>
            <li>
              <Link className=' nav-item nav-link' to='/'>Beranda</Link>
            </li>
            <li>
              <Link className=' nav-item nav-link' to='/tentang'>Tentang</Link>
            </li>
            <li className='dropdown'>
              <button className='nav-drop border-0 nav-item nav-link dropdown-toggle dropdown-toggle-split' data-bs-toggle="dropdown">
                <span className='visually-hidden'>navbar-dropdown</span>
              </button>
              <div className='dropdown-menu' style={{ backgroundColor: 'skyblue' }}>
                <Link className='dropdown-item text-secondary' style={{ backgroundColor: 'skyblue', fontSize: 13 }} to='/kalkulator'>
                  Kalkulator
                </Link>
                <a href="https://jeesgit.github.io/jeesApp/" target="_blank" className="text-decoration-none text-secondary ms-3" style={{ fontSize: 14 }} >
                  Todolist
                </a>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}

export default Navbar;
