import React from 'react';
import styles from './Project1.module.css';

var tampilanOutput;
var arrayOutput = [];
var copyArrayOutput;
var elementDitake;
var writeOutput2 = '';
var sisaElement2;
var objectInputNumber;
var objectInputOperation;
var saveObject = [];
var seeArray;
var useString;
var objectString;
var wroteOutput;
var readString;
var objectOutput;
var prosesString;
var stringOutput;
var objectOutputEquals;
var objectClear;
var objectClearAll;
var objectDitake;



function Project() {
    function MakeObject(objectLastOperation) {

        this.objectLast = objectLastOperation;

    }

    function MakeString(stringOutput) {
        this.lastString = stringOutput;
    }

    const prosesInputNumber = (e) => {
        tampilanOutput = document.querySelector('output#hasil');

        arrayOutput.push(e.target.innerHTML);
        seeArray = arrayOutput.length;
        if (seeArray < 21) {
            tampilanOutput.innerHTML += e.target.innerHTML;
            useString = tampilanOutput.innerHTML;
            writeOutput2 += e.target.innerHTML;
            objectString = new MakeString(useString);
        }
        else {
            arrayOutput.length = 20;
            tampilanOutput.innerHTML = objectString.lastString;
        }

        objectInputNumber = new MakeObject();
        saveObject.push(objectInputNumber);
    }


    const prosesInputOperation = (e) => {

        arrayOutput.pop();
        arrayOutput.push(e.target.innerHTML);
        sisaElement2 = writeOutput2.slice(0, -1) + e.target.innerHTML;
        tampilanOutput.innerHTML = sisaElement2;
        writeOutput2 = tampilanOutput.innerHTML;

        objectInputOperation = new MakeObject();
        saveObject.push(objectInputOperation);

    }

    const handleNumberClick = (e) => {
        console.log(e.target.innerHTML)
        if (e.target.innerHTML === '1') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '2') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '3') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '4') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '5') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '6') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '0') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '7') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '8') {
            prosesInputNumber(e);
        }
        if (e.target.innerHTML === '9') {
            prosesInputNumber(e);
        }

    }

    const handleOperationClick = (e) => {

        copyArrayOutput = arrayOutput.concat();
        elementDitake = copyArrayOutput.pop();

        switch (elementDitake) {
            case "*":
            case "/":
            case "+":
            case "-":
            case ".": prosesInputOperation(e);
                break;
            case "9":
            case "8":
            case "7":
            case "6":
            case "5":
            case "4":
            case "3":
            case "2":
            case "1":
            case "0": prosesInputNumber(e);
                break;
            default: tampilanOutput.innerHTML += "";
        }
    }

    const handleEqualClick = (e) => {
        console.log(writeOutput2);
        console.log(typeof writeOutput2);

        wroteOutput = eval(writeOutput2);
        console.log(wroteOutput);
        console.log(typeof wroteOutput);

        readString = wroteOutput.toString().length;
        if (readString < 21) {
            tampilanOutput.innerHTML = wroteOutput;
            arrayOutput = wroteOutput.toString().split('');
            writeOutput2 = tampilanOutput.innerHTML;
        }
        else {

            prosesString = wroteOutput.toPrecision(20);
            tampilanOutput.innerHTML = prosesString;
            arrayOutput = prosesString.toString().split('');
            writeOutput2 = tampilanOutput.innerHTML;

        }

        objectOutput = {
            clearOutput: function () {
                stringOutput = tampilanOutput.innerHTML.toString().slice(0, -1);
                tampilanOutput.innerHTML = stringOutput;
                arrayOutput = stringOutput.split('');
                writeOutput2 = tampilanOutput.innerHTML;

                var objectClearOutput = new MakeObject();
                saveObject.push(objectClearOutput);

            }
        }

        objectOutputEquals = new MakeObject(objectOutput);
        saveObject.push(objectOutputEquals);
    }

    const handleClearAll = (e) => {

        tampilanOutput.innerHTML = "";
        arrayOutput.length = 0;
        writeOutput2 = '';
        objectClear = {
            clear: function () {
                tampilanOutput.innerHTML = "";
            }
        };

        objectClearAll = new MakeObject(objectClear);
        saveObject.push(objectClearAll);

    }

    const handleDeleteClick = (e) => {
        var lastOperation;

        copyArrayOutput = saveObject.concat();
        objectDitake = copyArrayOutput.pop();

        if (objectDitake !== objectOutputEquals) {
            arrayOutput.pop();
            useString = writeOutput2.slice(0, -1);
            tampilanOutput.innerHTML = useString;
            writeOutput2 = tampilanOutput.innerHTML;
        }
        else if (objectDitake === objectClearAll) {
            objectClearAll.objectLast.clear();
        }
        else {
            lastOperation = objectOutputEquals.objectLast.clearOutput();
        }
    }

    const handleSummaryClick = (e) => {

        e.target.style.outlineWidth = '0px';
        e.target.style.outlineStyle = 'none';
        e.target.style.outlineColor = 'transparant';
        e.target.style.borderColor = 'transparant';

    }

    return (
        <div className='container-fluid'>
            <div class="kalkulatorNRiwayat">
                <div class="kalkulator">
                    <p className={styles.hnoContainer}>
                        <output id='hasil' className={styles.hasilOutput}>

                        </output>
                    </p>
                    <div className={styles.hrStyle2}>
                        &nbsp;
    </div>
                    <div className={styles.deleteContainer}>
                        <div className={styles.clearAll} onClick={handleClearAll}>
                            &lang;
        </div>
                        <div className={styles.delete} onClick={handleDeleteClick}>
                            &laquo;
        </div>
                    </div>

                    <div class={styles.boxContainer}>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name="seven">7</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='eight'>8</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='nine'>9</div>
                        <div className={styles.boxNDisplay1} style={{ fontSize: 50, color: 'red' }} onClick={handleOperationClick}>/</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='four'>4</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='five'>5</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='six'>6</div>
                        <div className={styles.boxNDisplay1} style={{ fontSize: 50, color: 'red' }} onClick={handleOperationClick}>*</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='one'>1</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='two'>2</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='three'>3</div>
                        <div className={styles.boxNDisplay1} style={{ fontSize: 50, color: 'red' }} onClick={handleOperationClick}>-</div>
                        <div className={styles.boxNDisplay2} onClick={handleNumberClick} name='zero'>0</div>
                        <div className={styles.boxNDisplay2} onClick={handleOperationClick}>.</div>
                        <div className={styles.boxNDisplay1} style={{ fontSize: 48, color: 'red' }} onClick={handleEqualClick}>=</div>
                        <div className={styles.boxNDisplay1} style={{ fontSize: 48, color: 'red' }} onClick={handleOperationClick}>+</div>
                    </div>
                </div>

                <div className={styles.riwayatKalkulator}>
                    <details className={styles.riwayatProject}>
                        <summary onClick={handleSummaryClick}>
                            riwayat
         </summary>
                        <p className={styles.paragrafRiwayat}>
                            Pada awalnya saya ingin menyertakan penggunaan CSS grid di dalam web. Dari situ saya membuat kalkulator yang tampilan dasarnya sedikit
                            mirip dengan yang di SamsungGalaxyA50S.
         </p>
                        <ol type="1">
                            <li>Prosedur
                    <ol style={{ listStyleType: 'decimal-leading-zero' }}>
                                    <li>Membuat tombol output</li>
                                    <li>Membuat tombol delete (&ltimes;) dan clearall (&comp;)</li>
                                    <li>Membuat container, tombol angka, dan tombol operasi aritmatika sederhana</li>
                                    <li>Menambahkan kode javascript untuk menghubungkan tombol-tombol diatas menjadi sebuah kalkulator</li>
                                </ol>
                            </li>
                            <li>Hambatan
                    <ul type="disc">
                                    <ul type="disc">
                                        <li>Membuat tampilan operasi perkalian dan pembagian sama pada sisi luar(tombol) dan output
                                    <ul type="circle">
                                                <li>Solusi
                                            <ul type="none">
                                                        <li>Pada awalnya tampilan tombol operasi dibuat menggunakan HTML entity, dengan tujuan dapat berfungsi
                                                        baik saat ditampilkan pada output hingga pemrosesan data. Namun tidak beroperasi saat di
                                                        Javascript. Tampilan diganti dengan operasi aritmatika agar bisa berfungsi hingga
                                                        pemrosesan data. Akibatnya, tampilan operasi di output sama dengan tampilan di tombol.
                                                </li>
                                                    </ul>
                                                </li>
                                            </ul>
                                        </li>
                                        <li>Memilih logika percabangan
                                    <ul type="circle">
                                                <li>Solusi
                                            <ul type="none">
                                                        <li>
                                                            Pada awalnya saya menggunakan if-else. Namun karena cara seleksinya melibatkan operator,
                                                            maka saya menggunakan switch-case. Switch-case hanya menyeleksi nilai.
                                                </li>
                                                    </ul>
                                                </li>
                                            </ul>
                                        </li>
                                    </ul>
                                </ul>
                            </li>
                            <li>Kekurangan
                    <ul type="disc">
                                    <li>
                                        Tampilan operasi perkalian dan pembagian di output sama seperti tampilannya di tombol.
                        </li>
                                </ul>
                            </li>
                        </ol>
                    </details>
                </div>

            </div>

            <div className='row bg-secondary mt-5'>
                <div className='col py-3 text-center'>
                    <footer className='btn-group btn-group-vertical'>
                        <a className='text-decoration-none text-light mb-2' href='josuasiregar47@gmail.com'>josuasiregar47@gmail.com</a>
                        <div className='btn-group d-flex justify-content-center'>

                            <span className='bi-instagram text-white'>
                                <a href="https://instagram.com/id.jees?igshd=ZDdkNTZiNTM" target="_blank" className="text-white text-decoration-none p-1">
                                    id.jees
                                </a>
                            </span>

                        </div>
                    </footer>
                    <p className='mb-0 mt-4 text-light'>
                        Copyright 2023 &copy; Josua Siregar
        </p>
                </div>
            </div>
        </div>
    )
}

export default Project;
